:root {
	/* Background */
	--background-primary: #181818;
	--background-secondary: #1e1e1e;
	--background-tertiary: #252525;
	--background-disabled: #3c3b3b;

	/* Normal State */
	--primary-normal: #1a1a1a;
	--secondary-normal: #252525;
	--tertiary-normal: #303030;

	/* Hovered State */
	--primary-hovered: #f39237;
	--primary-hover-border: #e0e0e080;
	--secondary-hovered: #3d3d3d;
	--secondary-hover-border: #e0e0e080;
	--tertiary-hovered: #474747;

	/* Active State */
	--primary-active: #4d4d4d;
	--secondary-active: #575757;
	--tertiary-active: #616161;

	/* In Focus State */
	--primary-focus: #666666;
	--secondary-focus: #707070;
	--tertiary-focus: #7a7a7a;

	/* Disabled State */
	--primary-disabled: #888888;
	--secondary-disabled: #929292;
	--tertiary-disabled: #9c9c9c;
	--background-disabled: #686868;

	/* Text Colors */
	--primary-text: #ffffff;
	--secondary-text: #e0e0e0;
	--tertiary-text: #b0b0b0;

	/* Text Shadows */
	--primary-text-shadow: #00000030;
	--primary-text-shadow-hovered: #00000035;
	--primary-text-shadow-active: #00000020;
	--primary-text-shadow-disabled: #00000030;

	--secondary-text-shadow: #00000040;
	--secondary-text-shadow-hovered: #00000045;
	--secondary-text-shadow-active: #00000030;
	--secondary-text-shadow-disabled: #00000040;

	--tertiary-text-shadow: #00000050;
	--tertiary-text-shadow-hovered: #00000055;
	--tertiary-text-shadow-active: #00000040;
	--tertiary-text-shadow-disabled: #00000050;

	/* Text Hovered, Active, Focus States */
	--primary-text-hovered: #000000;
	--secondary-text-hovered: #1a1a1a;
	--tertiary-text-hovered: #c2c2c2;

	--primary-text-active: #000000;
	--secondary-text-active: #1a1a1a;
	--tertiary-text-active: #303030;

	--primary-text-focus: #000000;
	--secondary-text-focus: #1a1a1a;
	--tertiary-text-focus: #303030;

	/* Dropdown shadows */
	--enabled-dropshadow: #ffffff42;
	--primary-dropshadow: #ffffff62;
	--secondary-dropshadow: #ffffff62;
	--tertiary-dropshadow: #ffffff62;

	/* Dialogue Nodes */
	--start-node: #013701;
	--start-node-border: #03d404;

	--answer-node: #062922;
	--answer-node-border: #199f85;

	--lead-node: #352205;
	--lead-node-border: #cc8413;

	--complete-node: #370101;
	--complete-node-border: #d50404;

	--complete-auto-node: #370101;
	--complete-auto-node-border: #d50404;

	--jump-node: #373838;
	--jump-node-border: #d4d5d;
}
