.landing-page-wrapper {
	display: flex;
	justify-content: space-around;
	align-items: stretch;
	border-radius: 10px;
	padding: 30px;
	background-color: var(--background-secondary);
	height: min(800px, 75%);
	width: min(1000px, 75%);
	flex-direction: column;
	flex-wrap: nowrap;
	align-content: space-between;
	gap: 2%;
	box-shadow: 0px 0px 16px 2px #ffffff3b;
}

.desktop-view {
	display: flex;
	justify-content: space-around;
	align-items: stretch;
	flex-direction: column;
	flex-wrap: nowrap;
	align-content: space-between;
	height: 100%;
}

.landing-page-button-container {
	padding-top: 20px;
	padding-bottom: 10px;
	min-width: 200px;
}

.landing-page-button {
	min-width: 150px;
}

hr {
	margin-left: 0;
	margin-right: 0;
}

.mobile-view {
	display: none;
}

@media (max-width: 750px) or (max-height: 800px) {
	.desktop-view {
		display: none;
		color: red;
	}

	.mobile-view {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		align-content: stretch;
		justify-content: space-around;
		align-items: stretch;
		height: 100%;
	}
}
